<template>
  <v-form v-model="valid" ref="serviceMappingForm">
    <v-row>
      <v-col cols="5">
        <v-select
          :items="updatedMappingListLeft"
          clearable
          v-model="selectedLeft"
          item-text="name"
          item-value="id"
          :rules="fieldValidations"
          :label="mappingSelectionLeftTitle"
          return-object
        ></v-select>
      </v-col>
      <v-col cols="5">
        <v-select
          :items="updatedMappingListRight"
          clearable
          v-model="selectedRight"
          item-text="name"
          :rules="fieldValidations"
          item-value="id"
          :label="mappingSelectionRightTitle"
          return-object
        ></v-select>
      </v-col>
      <v-col cols="2" class="flex justify-center align-center">
        <rbx-button :block="true" color="pink" :click="addMapping" title="Add"></rbx-button>
      </v-col>
    </v-row>
  </v-form>
</template>
<script>
import RbxButton from '../RbxButton';
import validation from '../../common/validation';
export default {
  components: { RbxButton },
  props: [
    'fromList',
    'toList',
    'mappingListLeft',
    'mappingListRight',
    'mappingRightKey',
    'mappingLeftKey',
    'serviceMappings',
    'createServiceMapping',
    'mappingSelectionLeftTitle',
    'mappingSelectionRightTitle',
  ],
  data() {
    return {
      valid: false,
      selectedToList: [],
      selectedLeft: null,
      selectedRight: null,
    };
  },
  computed: {
    mappings() {
      return this.serviceMappings;
    },
    selectedMappingListLeft() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.mappingLeftKey]);
    },
    selectedMappingListRight() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.mappingRightKey]);
    },
    updatedMappingListLeft() {
      return this.updateLisItemProperty(this.mappingListLeft, this.selectedMappingListLeft);
    },
    updatedMappingListRight() {
      return this.updateLisItemProperty(this.mappingListRight, this.selectedMappingListRight);
    },
    fieldValidations() {
      return [validation.required];
    },
  },
  methods: {
    addMapping() {
      const isValid = this.$refs.serviceMappingForm.validate();
      if (!isValid) {
        return;
      }
      this.selectedMappingListLeft.push(this.selectedLeft);
      this.selectedMappingListRight.push(this.selectedRight);
      this.createServiceMapping({
        [this.mappingLeftKey]: this.selectedLeft,
        [this.mappingRightKey]: this.selectedRight,
      });
      this.selectedLeft = null;
      this.selectedRight = null;
      this.$refs.serviceMappingForm.reset();
    },
    updateLisItemProperty(list, selectedItems) {
      if (!selectedItems) {
        return list;
      }
      const selectedListItems = selectedItems.filter(item => item).map(item => item.id);

      return list.reduce((acc, curr) => {
        if (selectedListItems.includes(curr.id)) {
          curr.disabled = true;
        } else {
          curr.disabled = false;
        }
        acc.push(curr);
        return acc;
      }, []);
    },
  },
};
</script>
<style scoped>
.flex {
  display: flex;
}
</style>
