<template>
  <v-data-table :loading="loading" :headers="headers" :items="items">
    <template v-slot:item.actions="{ item }">
      <v-btn @click="() => deleteItem(item)" icon small color="error" class="white--text">
        <v-icon small dark> mdi-delete </v-icon>
      </v-btn>
    </template>
  </v-data-table>
</template>
<script>
export default {
  name: 'service-mapping-grid',
  props: [
    'loading',
    'mappingRightKey',
    'mappingLeftKey',
    'serviceMappings',
    'deleteItem',
    'mappingRightTitle',
    'mappingLeftTitle',
  ],
  data() {
    return {
      headers: [
        {
          text: this.mappingLeftTitle,
          align: 'start',
          value: 'serviceLeft',
        },
        {
          text: this.mappingRightTitle,
          align: 'start',
          value: 'serviceRight',
        },
        {
          text: 'Actions',
          align: 'start',
          value: 'actions',
          sortable: false,
        },
      ],
    };
  },
  computed: {
    items() {
      return this.serviceMappings.map(val => ({
        ...val,
        serviceLeft: val[this.mappingLeftKey].name,
        serviceRight: val[this.mappingRightKey].name,
      }));
    },
  },
};
</script>
